@use '../intial/index' as *;

// For Image "Thumbnailing" in various sections
// Example - Screenshots on Linux Distro and Desktop homepages

.thumbnails {
  padding: 2rem;

  &--lg {
    @extend .thumbnails;
    max-height: 450px;
  }

  &--sm {
    @extend .thumbnails;
    max-height: 350px;
  }
}

// it looks like i forgot to paste the code from img-fluid here
.thumbnail-fluid {
	max-width: 100%;
	height: auto;

  &--sm {
    @extend .thumbnail-fluid;
    @include thumbnail--sm;
  }

  &--lg {
    @extend .thumbnail-fluid;
    @include thumbnail--lg;
  }
}

// ?? what is this for?
.list-group-item {
  border: 0;
}

// May not be needed if things are responsive via Bootstrap
.YTPlaylists {
  width: 560px;
  height: 315px;
}

/****** STARTPAGE Box related ******************/

#SPSearch {
  margin: 10px auto;

  // For the SP icon and input box
  .SPHeightIconInput {
    height: 35px;
  }

  .SPSearchIcon {
    height: 33px;
    vertical-align: middle;
  }
}

.shadow {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

/************** RSS Feed *************/
// For GAOTD too
.feed-item-title {
  a {
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.4rem;
    display: inline-block;
    margin: 0 auto;
  }
}

.feed-item-desc {
  img {
    display: block;
    max-width: 100px;
    height: auto;
    margin: 1rem auto;
  }
}

/*********** End RSS CSS **********/

#outputSection a {
  color: var(--ThemeBGColorAccent);
}

// Footer specially (for now?) in the TechHQ side of the site
#techHQFooter {
  display: flex;
  flex-direction: row;
  padding-left: 20px;

  a {
    color: inherit;
    margin: 0 1rem;
    font-weight: 500;
  }

  a:hover {
    outline: 1px dotted black;
    text-decoration: none;
  }
}


// is there a need for this to be so hyper-specific? yes (tip: look at grid-template-area)
// Place in the "card" div
.softwareCard {
  .card__header--wide {

    grid-template-areas: 'program-title platform';

    .program-title {
      grid-area: program-title;
      align-self: flex-start;
    }

    .platform {
      grid-area: platform;
    }
  }
}

// For Distro and Desktop Frontpages on the LinuxHQ side of the site
#linuxFrontPrimaryContent {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;

  .card {
    min-width: 300px;
    max-height: none;
  }
}

// Media Query
// why is this here? ... shouldn't this be in 02-layout somewhere?
@media (max-width: 794px) {
  .sidebar {
    .card {
      margin-left: 0;
      margin-bottom: 1rem;
    }
  }

  #linuxFrontPrimaryContent {
    .card {
      min-width: 300px;
      padding: 1rem;
    }
  }
}

// not sure i like how hyper specific this is but it does let me clean up bs css class calls in the html
// location: techHQ frontpage
#EducationalSites {
  justify-content: space-around;
  margin-bottom: 0 auto 25px auto;
  text-align: center;
  width: 75%;

  .card__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    a {
      align-content: center;

      img {
        margin: 0 auto;
        padding: 10px 1.5rem;
      }
    }

    .card__header {
      font-size: 24px;
      font-weight: 700;
    }
  }
}

// For LinuxHQ distro section and eventually more
#desktopSection {
  .card {
    box-shadow: none;
    border: 1px dotted var(--themeBGColorMain);

    // putting this here since there is a max-height (still need to lookup why i did that) on the main primaryContent .card heigh
    max-height: none;
  } // closes card
} // closes #desktopSection

#linuxVersions {
  // the top level of the column
  .distroBaseTitle {
    text-decoration: underline;
    font-weight: 900;
  }

  // The Titles of the Distro/Versions
  .versionTitle {
    margin-right: 0.5rem;
  }

  // the actual version number
  .version {
    color: var(--themeBGColorAccent);
  }
}

// New idea for screen readers
.hiddenAccessLinkText {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

code {
  @include darkBG;

  display: inline-block;
}
